<template>
    <div>

        <a-divider />
        <p>先来上传一张原图吧~: </p>
        <div style="width: 1100px;height:150px;margin: 0 auto">
            <a-upload-dragger
                    name="src_image"
                    :multiple="true"
                    method = "post"
                    :beforeUpload="beforeUpload"
                    action="/api/image/encode/"
                    @change="handleChange"
            >
                <p class="ant-upload-drag-icon">
                    <a-icon type="inbox" />
                </p>
                <p class="ant-upload-text">Click or drag file to this area to upload</p>
                <p class="ant-upload-hint">
                    Support for a single upload. Strictly prohibit from uploading company data or other
                    band files
                </p>
            </a-upload-dragger>
        </div>
        <a-divider />
        <a-divider />
        <div id="verifyArea" v-show="show">
            <p>下面为您呈上3张添加了水印的图片~</p>
            <p>注意哦,下面每张图片都是不同滴,但是肉眼分辨不出来</p>
            <p>每张图片的名字蕴含了用户的ID</p>
            <p>您可以分别点击下载</p>

            <div style="width: 270px;margin: 0 auto">
                <ul>
                    <li><a @click="downloadIamge(newImg1Url,fileName1)">模拟用户1下载的图片</a></li>
                    <br>
                    <li><a @click="downloadIamge(newImg2Url, fileName2)" >模拟用户2下载的图片</a></li>
                    <br>
                    <li><a @click="downloadIamge(newImg3Url, fileName3)" >模拟用户3下载的图片</a></li>
                </ul>
            </div>
            <a-divider />
            <a-divider />
            <p>您可以将水印图片重命名或拍照再上传于此处</p>
            <p>我会告诉亲对应此水印图片的用户id</p>
            <div style="width: 1100px;height:150px;margin: 0 auto">
                <a-upload-dragger
                        name="marked_image"
                        :multiple="true"
                        method = "post"
                        :beforeUpload="beforeUpload"
                        action="/api/image/decode/"
                        @change="handleChange2"
                >
                    <p class="ant-upload-drag-icon">
                        <a-icon type="inbox" />
                    </p>
                    <p class="ant-upload-text">Click or drag file to this area to upload</p>
                    <p class="ant-upload-hint">
                        Support for a single upload. Strictly prohibit from uploading company data or other
                        band files
                    </p>
                </a-upload-dragger>
            </div>
            <br>
            <br>
            <p v-show="showUid">此水印对应的用户ID为: {{uid}}</p>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                show: false,
                showUid: false,
                uid: "",
                newImg1Url: "#",
                newImg2Url: "#",
                newImg3Url: "#",
                fileName1: "1.jpg",
                fileName2: "1.jpg",
                fileName3: "1.jpg"
            };
        },
        methods: {
            handleChange(info) {
                const status = info.file.status;
                if (status !== 'uploading') {
                    console.log(info.file, info.fileList);
                }
                if (status === 'done') {
                    // this.
                    this.show = true;
                    this.newImg1Url = info.file.response.data.downloadlist[0].download;
                    this.newImg2Url = info.file.response.data.downloadlist[1].download;
                    this.newImg3Url = info.file.response.data.downloadlist[2].download;
                    this.fileName1 = this.newImg1Url.substring(this.newImg1Url.lastIndexOf("\/")+1,this.newImg1Url.length);
                    this.fileName2 = this.newImg2Url.substring(this.newImg2Url.lastIndexOf("\/")+1,this.newImg1Url.length);
                    this.fileName3 = this.newImg3Url.substring(this.newImg3Url.lastIndexOf("\/")+1,this.newImg1Url.length);

                    this.$message.success(`${info.file.name} file uploaded successfully.`);
                } else if (status === 'error') {
                    this.$message.error(`${info.file.name} file upload failed.`);
                }
            },
            beforeUpload(file) {
                const isJpgOrPng = file.type === 'image/jpeg';
                if (!isJpgOrPng) {
                    this.$message.error('You can only upload JPG file!');
                }
                const isLt3M = file.size / 1024 / 1024 < 3;
                if (!isLt3M) {
                    this.$message.error('Image must smaller than 3MB!');
                }
                return isJpgOrPng && isLt3M;
            },
            handleChange2(info) {
                const status = info.file.status;
                if (status !== 'uploading') {
                    console.log(info.file, info.fileList);
                }
                if (status === 'done') {
                    console.log(info.file.response);
                    this.uid = info.file.response.data.id;
                    this.showUid = true;
                    this.$message.success(`${info.file.name} file uploaded successfully.`);
                } else if (status === 'error') {
                    this.$message.error(`${info.file.name} file upload failed.`);
                }
            },
            downloadIamge(imgsrc, name) {//下载图片地址和图片名
                var image = new Image();
                // 解决跨域 Canvas 污染问题
                image.setAttribute("crossOrigin", "anonymous");
                image.onload = function() {
                    var canvas = document.createElement("canvas");
                    canvas.width = image.width;
                    canvas.height = image.height;
                    var context = canvas.getContext("2d");
                    context.drawImage(image, 0, 0, image.width, image.height);
                    var url = canvas.toDataURL("image/png"); //得到图片的base64编码数据

                    var a = document.createElement("a"); // 生成一个a元素
                    var event = new MouseEvent("click"); // 创建一个单击事件
                    a.download = name || "photo"; // 设置图片名称
                    a.href = url; // 将生成的URL设置为a.href属性
                    a.dispatchEvent(event); // 触发a的单击事件
                };
                image.src = imgsrc;
            },


        },
    };
</script>
<style scoped="scoped">
    p {
        font-size: 1.3em;
    }
    ul li{
        position: relative;
        font-size: 1.5em;
        font-family: STXinwei;

    }

</style>
