<template>
	<div>
		<a-table :columns="columns" :rowKey="record => record.pictureId" :dataSource="data" :pagination="pagination" :loading="loading"
		 @change="handleTableChange">

			<template slot="operation" slot-scope="text, record">
				<a-button type="primary" @click="info">分享</a-button>
				<a-popconfirm v-if="data.length" title="Sure to delete?" @confirm="() => onDelete(record.key)">
					<!-- <a href="javascript:;">Delete</a> -->
					<a-button type="primary" style="background-color: red;">Delete</a-button>
				</a-popconfirm>
			</template>
		</a-table>

	</div>
</template>
<script>
	import reqwest from 'reqwest';
	const columns = [{
			title: '图片ID',
			dataIndex: 'pictureId',
			sorter: true,
			width: '13%',
		},
		{
			title: '所属文件夹',
			dataIndex: 'folder',
			width: '16%',
		},
		{
			title: '图片名称',
			dataIndex: 'pictureName',
			width: '16%',
		},
		{
			title: '上传时间',
			dataIndex: 'uploadTime',
			width: '16%',
		},
		{
			title: '图片格式',
			dataIndex: 'pictureFormat',
			width: '13%',
		},
		{
			title: '操作',
			dataIndex: 'operation',
			scopedSlots: {
				customRender: 'operation'
			},
			width: '13%',
		}
	];

	export default {
		mounted() {
			this.fetch();
		},
		data() {
			return {
				data: [],
				pagination: {},
				loading: false,
				columns,
				isShowAboutDialog: false,
				visible: false,
			};
		},
		methods: {
			handleTableChange(pagination, filters, sorter) {
				console.log(pagination);
				const pager = { ...this.pagination
				};
				pager.current = pagination.current;
				this.pagination = pager;
				this.fetch({
					results: pagination.pageSize,
					page: pagination.current,
					sortField: sorter.field,
					sortOrder: sorter.order,
					...filters,
				});
			},
			fetch(params = {}) {
				console.log('params:', params);
				this.loading = true;
				reqwest({
					url: 'http://localhost:3000/user/picture',
					method: 'get',
					data: {
						// results: 10,
						...params,
					},
					type: 'json',
				}).then(data => {
					const pagination = { ...this.pagination
					};
					// Read total count from server
					// pagination.total = data.totalCount;
					pagination.total = 200;
					this.loading = false;
					this.data = data.results;
					this.pagination = pagination;
				});
			},
			onDelete(key) {
				const data = [...this.data];
				this.data = data.filter(item => item.key !== key);
			},
			info() {
				const h = this.$createElement;
				this.$info({
					title: '分享',
					content: h('div', {}, [
						h('p', '已生成分享链接，复制发送给朋友吧~'),
						h('p', 'https://www.tupian.com/1.jpg'),
						// h('a-icon type="copy"',null)
					]),
					onOk() {},
				});
			},

		}
	};
</script>
