<template>
	<div>
		<div class="top-main">
			<h2>图片管理平台</h2>
		</div>
		<div class="head-main">
			<a-row>
				<a-col :span="3" style="height: 75px;">
					<div class="btn-head">
						<a-button type="primary" @click="showModal">上传图片</a-button>

						<a-modal title="上传图片" :visible="visible" @ok="handleOk" :confirmLoading="confirmLoading" @cancel="handleCancel">
							<a-form :form="form" @submit="handleSubmit">
								<a-form-item label="选择图片" :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
									<a-upload name="file" :multiple="true" action="https://www.mocky.io/v2/5cc8019d300000980a055e76" :headers="headers"
									 @change="handleChange">
										<a-button>
											<a-icon type="upload" /> 点击选择图片 </a-button>
									</a-upload>
								</a-form-item>
								<p>只能上传jpg/png文件，且不超过500kb</p>
								<a-form-item label="选择文件夹 " :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
									<a-select style="width: 80%" defaultValue="list-a" class="input-head">
										<a-select-option value="list-a">list-a</a-select-option>
										<a-select-option value="list-b">list-b</a-select-option>
									</a-select>
								</a-form-item>
							</a-form>
						</a-modal>
					</div>
				</a-col>
				<a-col :span="3" style="height: 75px;">
					<div class="btn-head">
						<router-link to="folder">
							<a-button type="primary" style="background-color: green;">文件夹管理</a-button>
						</router-link>
					</div>
				</a-col>
				<a-col :span="3" style="height: 75px;">
					<div class="btn-head">
						<router-link to="decryption">
							<a-button type="primary">水印验证</a-button>
						</router-link>
					</div>
				</a-col>
				<a-col :span="4">
					<a-select style="width: 80%" defaultValue="Home" class="input-head">
						<a-select-option value="Home">Home</a-select-option>
						<a-select-option value="Company">Company</a-select-option>
					</a-select>
				</a-col>
				<a-col :span="6">
					<a-input-search placeholder="input search text" @search="onSearch" enterButton class="input-head" />
				</a-col>
			</a-row>
		</div>
		<div>
			<keep-alive>
				<router-view></router-view>
			</keep-alive>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				visible: false,
				confirmLoading: false,
				headers: {
					authorization: 'authorization-text',
				},
				formLayout: 'horizontal',
				form: this.$form.createForm(this, {
					name: 'coordinated'
				}),

			};
		},
		methods: {
			onSearch(value) {
				console.log(value);
			},
			showModal() {
				this.visible = true;
			},
			handleOk(e) {
				this.ModalText = 'The modal will be closed after two seconds';
				this.confirmLoading = true;
				setTimeout(() => {
					this.visible = false;
					this.confirmLoading = false;
				}, 2000);
			},
			handleCancel(e) {
				console.log('Clicked cancel button');
				this.visible = false;
			},
			handleChange(info) {
				if (info.file.status !== 'uploading') {
					console.log(info.file, info.fileList);
				}
				if (info.file.status === 'done') {
					this.$message.success(`${info.file.name} file uploaded successfully`);
				} else if (info.file.status === 'error') {
					this.$message.error(`${info.file.name} file upload failed.`);
				}
			},
			handleSubmit(e) {
				e.preventDefault();
				this.form.validateFields((err, values) => {
					if (!err) {
						console.log('Received values of form: ', values);
					}
				});
			},
			handleSelectChange(value) {
				console.log(value);
				this.form.setFieldsValue({
					note: `Hi, ${value === 'male' ? 'man' : 'lady'}!`,
				});
			}
		},
	}
</script>
<style scoped="scoped">
	.top-main {
		height: 50px;
		background-color: #0000ED;
		text-align: left;
	}

	.top-main h2 {
		margin-left: 50px;
		padding-top: 8px;
		color: white;
	}

	.head-main {
		height: 75px;
		/* background-color: #00FF00; */
		text-align: center
	}

	.btn-head {
		height: 100%;
		padding-top: 20px;
	}

	.input-head {
		padding-top: 20px;
	}

	a-col {
		background-color: #00FF00;
	}
</style>
