import Vue from 'vue'
import Antd from 'ant-design-vue'
import App from './App.vue'
import 'ant-design-vue/dist/antd.css';
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'
// @相当于项目路径,默认是src

import Layout from '@/components/Layout.vue'
import LoginOrRegister from '@/components/LoginOrRegister.vue'
import Login from './components/loginAndRegister/Login.vue'
import Register from './components/loginAndRegister/Register.vue'
import PictureForm from './components/home/PictureForm.vue'
import FolderForm from './components/home/FolderForm.vue'
import Decryption from "./components/home/Decryption"


Vue.config.productionTip = false

Vue.use(Antd)
Vue.use(VueRouter)
Vue.use(VueResource)

const router = new VueRouter({
	// url中没有/#
	mode: 'history',
	routes: [
		{
			path: '/loginOrRegister',
			component: LoginOrRegister,
			children: [
				{
					path: 'login',
					component: Login
				},
				{
					path: 'register',
					component: Register
				}
			]
		},
		{
			path: '/home',
			component: Layout,
			children: [
				{
					path: 'picture',
					component: PictureForm
				},
				{
					path: 'folder',
					component: FolderForm
				},
				{
					path: 'decryption',
					component: Decryption
				}
			]
		},
		{
			path: '/',
			redirect: '/home/decryption'
		}
	]
})

new Vue({
	router,
  render: h => h(App),
}).$mount('#app')
